.descriptionBlog {
    background-color: #ecdc45;
    display: flex;
    justify-content: space-between;
    border-radius: 5px;
    border: black 3px solid;
    width: 94.5%;
}

.profileImage {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-wrap: wrap;
    border: black solid 3px;
    width: 25%;
    height: 100%;
    margin: 10px 10px 10px 15px;
}
.profileInformation {
    width: 65%;
    margin: 10px;
}
.avatar {
    width: 85%;
    color: #ecdc45;
}